<template>
  <div>
    <el-row>
      <el-col :span="14" :offset="5">
        <!--  -->
        <el-breadcrumb separator="/" class="breadcrumb">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/gallery' }"
            ></el-breadcrumb-item
          >
          <el-breadcrumb-item>{{
            crumb[this.$route.query.itemType]
          }}</el-breadcrumb-item>
        </el-breadcrumb>
        <el-divider></el-divider>
      </el-col>
      <!--  -->
      <el-col :span="14" :offset="5">
        <el-table :data="projectList" stripe fit>
          <el-table-column prop="project_name" label="" width="180">
          </el-table-column>
          <el-table-column prop="description" label="" width="600">
          </el-table-column>
          <el-table-column label="">
          <template slot-scope="scope" width="180px">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top">
              <el-button type="primary" icon="el-icon-tickets" size="mini" @click="showDetail(scope.row.id)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      // 
      crumb: {
        Academic: "",
        Achievements: "",
        Business: "",
      },
      // 
      projectList: [
        {
          id: 111,
          project_name: "test1",
          description: "test1",
        },
        {
          id: 222,
          project_name: "test2",
          description: "test2",
        },
      ],
    };
  },
  methods: {
    // 
    async getProjectList() {
      const index = {
       'Business': 1,
       'Academic': 2,
       'Achievements': 3,
      }
      const typeNum = index[this.$route.query.itemType]
      const { data:res } = await this.$axios.get(`/project/getbyType/${typeNum}`)
      if(res.status === 200) {
        console.log(res.data)
        return this.projectList = res.data.records
      }
      else {
        return this.$message.error('')
      }
    },
    // 
    backToHome() {
      this.$router.push("/");
    },
    // 
    showDetail(id) {
      console.log('id'+id)
      this.$router.push({ path: `/itemdetail`, query: { itemid: id } });
    }
  },
  mounted() {
    // 
    console.log('',this.$route.query.itemType)
    this.getProjectList()
  },
};
</script>

<style scoped>
.bg-purple-dark {
  background: #99a9bf;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-breadcrumb {
  margin-bottom: 25px;
  margin-top: 15px;
}
.formBox {
  background-color: #f0f0f0;
}

.el-form {
  padding: 20px;
}
</style>
